import React, { useState, useEffect } from 'react'
import { Link } from 'gatsby'
import cx from 'classnames'

import moment from 'moment'
import 'moment/locale/ru'

import styles from './Aside.module.scss'
import helpers from '../../helpers'

import calendar from '../../images/calendar.svg'
import arrowDown from '../../images/arrowDown.svg'
import arrowUp from '../../images/arrowUp.svg'

function Aside() {
    const [now, setNow] = useState(moment())

    useEffect(() => {
        const timer = setInterval(() => setNow(moment(new Date())), 1000)
        return () => clearInterval(timer)
    }, [now])

    const { LinksSelect, LinksFooter } = helpers.constants.links

    return (
        <aside className={styles.aside}>
            <div className={styles.aside__container}>
                <div className={styles.user}>
                    <div className={styles.user__name}>Григоренко И. О.</div>
                    <div className={styles.user__id}>БС №1234-БА</div>
                </div>

                <div className={styles.time}>
                    <div className={styles.time__date}>
                        <img src={calendar} alt="calendar" />
                        <span>
                            {now.format('dddd')}
                            <br />
                            {now.format('DD/MM/YYYY')}
                        </span>
                    </div>
                    <div className={styles.time__clock}>
                        {now.format('HH:mm')}
                    </div>
                </div>

                <div className={styles.rate}>
                    <span className={styles.rate__title}>Курс валюты</span>
                    <div className={styles.rate__container}>
                        <span
                            className={cx([styles.rate__text, {
                                [styles.rate_upgraded]: false,
                                [styles.rate_downgraded]: true,
                            }])}
                        >
                            $: 67 руб.
                            <RateSolver />
                        </span>
                        <span
                            className={cx([styles.rate__text, {
                                [styles.rate_upgraded]: true,
                                [styles.rate_downgraded]: false,
                            }])}
                        >
                            €: 75 руб.
                            <RateSolver />
                        </span>
                    </div>
                </div>
                <nav className={styles.select}>
                    {LinksSelect.map(l => (
                        <div className={styles.navLink} key={l.text}>
                            <img src={l.icon} alt={l.text} />
                            <Link to={l.link}>{l.text}</Link>
                        </div>
                    ))}
                </nav>
                <footer className={styles.footer}>
                    {LinksFooter.map(l => (
                        <div className={styles.navLink} key={l.text}>
                            <img src={l.icon} alt={l.text} />
                            <Link to={l.link}>{l.text}</Link>
                        </div>
                    ))}
                </footer>
            </div>
        </aside>
    )
}

const RateSolver = () => (
    <div className={styles.arrows}>
        <img className={cx([styles.arrows__arrowUp, styles.arrows__arrow])} src={arrowUp} alt="upgraded" />
        <img className={cx([styles.arrows__arrowDown, styles.arrows__arrow])} src={arrowDown} alt="downgraded" />
    </div>
)

export default Aside
